.footer {
  background-color: #f2f2f2;
}

.footer__card:not(.footer__card--index) {
  background-color: $grey-bg;
}

.footer__card--index {
  background-color: #ffffff;
}

.footer__container {
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer__card-catalog {
  background-color: $grey-bg;
}

.footer__card-form {
  padding-bottom: 3px;
}

.footer__welcome {
  margin: 0;
  width: 140px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.footer__adress {
  margin: 0;
  width: 140px;
  line-height: 20px;
  font-family: $second-font;
}

.footer__adress-city {
  margin: 0;
}

.footer__container-map {
  position: relative;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-bottom: 0;
  background-position: center -44px;
  background-color: $grey-bg;
  background-image: url("../img/bg-map-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-map-mobile@2x.jpg");
  }
}

