.footer__map {
  width: 100%;
  height: 367px;
}

.footer__container-bottom {

  padding-bottom: 22px;
  background-color: $grey-bg;
}

@media (min-width: 500px) {
  .footer__container-map {
    background-position: center;
  }
}

@media (min-width: $tablet-width) {
  .footer__container {
    padding: 64px 70px;
  }

  .footer__welcome {
    font-size: 20px;
    line-height: 25px;
  }

  .footer__adress-wrapper {
    display: flex;
    flex-direction: column;
  }

  .footer__adress,
  .footer__adress-city {
    font-size: 16px;
    line-height: 20px;
    text-align: right;
    width: 160px;
  }

  .footer__adress-city {
    margin-top: auto;
  }

  .footer__container-map {
    background-image: url("../img/bg-map-tablet@1x.jpg");
    background-position: 100% -7px;
    min-height: 400px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-tablet@2x.jpg");
    }
  }

  .footer__map {
    height: 400px;
  }

  // .footer__container-map::before {
  //   left: 42%;
  //   top: 36%;

  //   width: 113px;
  //   height: 106px;
  // }

  .footer__container-bottom {
    padding-top: 25px;
  }
}

@media (min-width: $desktop-width) {
  .footer {
    position: relative;
    background-color: #f2f2f2;
  }

  .footer__card {
    z-index: 10;
  }

  .footer__container {
    background-color: #ffffff;
    z-index: 10;
    position: absolute;
    top: 90px;
    left: 50%;
    margin-left: -610px;
    width: 564px;
    padding: 70px 77px;
    min-width: auto;
  }


  .footer__adress {
    text-align: left;
  }

  .footer__container-map {
    background-image: url("../img/bg-map-desktop@1x.jpg");
    background-position: 100% -7px;
    background-size: cover;
    width: 100%;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-desktop@2x.jpg");
    }
  }

  // .footer__container-map::before {
